import React from 'react';
import {
  List,
  ListItem,
  ListItemSecondaryAction,
  ListItemText,
  ListItemAvatar,
  Divider
} from '@material-ui/core';
import Skeleton from '@material-ui/lab/Skeleton';
import { makeStyles } from '@material-ui/core/styles';
import { Link } from "react-router-dom";

import { Routes } from '../../Routes'

import TimeAgo from 'timeago-react';
import {register} from 'timeago.js';
import ru from 'timeago.js/lib/lang/ru';
register('ru', ru);

const useStyles = makeStyles(theme => ({
    root: {
        paddingTop:0,
    },
    item:{
        height:props => props.type==='min'?140:190,
        paddingTop:0,
    },
    itemImg:{
        height: props => props.type==='min'?130:160,
        marginRight:theme.spacing(2),
    },
    itemImgLoading:{
      height: props => props.type==='min'?130:160,
      width: props => props.type==='min'?92:114,
      marginRight:theme.spacing(2),
      animation: 'MuiSkeleton-keyframes-pulse 1.5s ease-in-out 0.5s infinite',
      backgroundColor: 'rgba(0, 0, 0, 0.04)'
    },
    chapterItem:{
        padding:0
    },
    title:{
      color:theme.palette.text.primary
    }
}));


  const MangaListItem = ({props,classes,size}) => (
    <>
    {
      props.list.map(item=>(
          <ListItem key={`update_list_${item.id}`} alignItems="flex-start" className={classes.item}>
          <ListItemAvatar>
          <Link to={Routes.Manga.link(item.id)}>
              <img src={item.imgUrl} className={classes.itemImg} ></img>
          </Link>
          </ListItemAvatar>
          <ListItemText
          primary={<Link className={classes.title} to={Routes.Manga.link(item.id)}>{item.title}</Link>}
          secondary={
              <>
              <List component="div">
                  <Divider />
                  
                  {
                      item.updates.chapters.map((chap,i)=>(
                      (i<(item.updates.chapters.length-1==item.updates.count?size:(size-1)))&&<ListItem 
                      key={`update_list_${item.id}_chap_${chap.id}`} 
                      className={classes.chapterItem}
                      component={Link} 
                      to={Routes.ReadManga.link(item.id, chap.id)} 
                      button 
                      >
                          
                          <ListItemText primary={`Том-${chap.volume} Глава-${chap.chapter}`} />
                          <ListItemSecondaryAction>
                            <TimeAgo
                            datetime={chap.createdAt}
                            locale='ru'
                            />
                          </ListItemSecondaryAction>
                      </ListItem>
                      ))
                  }
                  {
                      (item.updates.count>3)
                      &&<ListItem button className={classes.chapterItem}
                      component={Link} 
                      to={Routes.Manga.link(item.id)} 
                      >
                          <ListItemText primary={`Показать еще ${item.updates.count-(size-1)} новых глав`} />
                      </ListItem>
                  }
                  </List>
              </>
          }
          />
          </ListItem>
      ))
    }
    </>
  )




  const MangaLoadListItem = ({props,classes,size}) => (
    <>
    {
      [...Array(5)].map((_,i1)=>(
          <ListItem key={`update_list_load${i1}`} alignItems="flex-start" className={classes.item}>
          <ListItemAvatar>
              <div className={classes.itemImgLoading}></div>
          </ListItemAvatar>
          <ListItemText
          primary={<Skeleton width={430}/>}
          secondary={
              <>
              <List component="div">
                  <Divider />
                  
                  {
                      ([69,70,73,75].slice(0,size)).map((percent,i2)=>(
                      <ListItem 
                      key={`update_list_load${i1}_chap_${i2}`} 
                      >
                          <Skeleton height={15} width={`${percent}%`} style={{ marginBottom: 0 }} />
                      </ListItem>
                      ))
                  }
                  </List>
              </>
          }
          />
          </ListItem>
      ))
    }
    </>
  )




  const UpdateList = props => {
    const classes = useStyles(props);
    const size = props.type==='min'?3:4
    console.log(props.isLoading)
    return(
      <div>

        <List className={classes.root}>
          <MangaListItem size={size} props={props} classes={classes}/>
          {props.isLoading&&
          <MangaLoadListItem size={size} props={props} classes={classes}/>
          }
        </List>
      </div>
    )}

  export default UpdateList;