import { observer, inject } from 'mobx-react';
import {
  Typography,
  MenuItem,
  FormControl,
  Select,
  InputBase,
  Button,
  SwipeableDrawer
} from '@material-ui/core';
import React, {useEffect} from 'react';

import { makeStyles, withStyles } from '@material-ui/core/styles';



import sortParams from '../sort.params'
import theme from '../theme'

import Filter from '../Components/filter'
import MangaUpdateList from '../Components/mangaUpdateList'
import useInfiniteScroll from "../Components/useInfiniteScroll";
import Hidden from '@material-ui/core/Hidden';
const FilterInput = withStyles({ root: { padding: "1.5px 4px 0 4px" }, selectMenu: { margin: theme.spacing(1), } })(InputBase);


const useStylesMenu = makeStyles({
  list: {
    width: 250,
  },
  fullList: {
    width: 'auto',
  },
});
const RMenu = ({mangaFilterStore}) => {
  const classes = useStylesMenu();
  const [state, setState] = React.useState({
    right: false,
  });

  const toggleDrawer = (anchor, open) => (event) => {
    if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const list = (anchor) => (
    <div style={{width: 320}}>
      <div style={{padding:16}}>
      <Typography component={'span'} color="textSecondary" style={{fontWeight: "450", margin:16}}>
          <div style={{padding:4}}>Фильтр ({mangaFilterStore.cnt||0})</div>
          { 
          <Button onClick={()=>mangaFilterStore.clearAll("MangaListStore")} disabled={mangaFilterStore.cnt===0} className={classes.filterResetButton} variant="outlined" size="small" color="secondary">
            Сбросить
          </Button>
          }
        </Typography>
      </div>
      <Filter mangaFilterStore={mangaFilterStore} storeName={"MangaListStore"}/>
    </div>
  );

  return (
    <div>
      {
        <React.Fragment key={'right'}>
          <Button onClick={toggleDrawer('right', true)}>Фильтр ({mangaFilterStore.cnt||0})</Button>
          <SwipeableDrawer
            anchor={'right'}
            open={state['right']}
            onClose={toggleDrawer('right', false)}
            onOpen={toggleDrawer('right', true)}
          >
            {list('right')}
          </SwipeableDrawer>
        </React.Fragment>
      }
    </div>
  );
}









const useStyles = makeStyles(theme => ({
  formControl: {
    // margin: theme.spacing(1),
    minWidth: 80,
    paddingLeft: 4,
  },
  filterResetButton: {
    marginLeft: theme.spacing(1),
  },
  topGroup: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    paddingTop: 72,
    marginBottom: theme.spacing(2),
  }
}));





const MangaListPage = observer(({mangaFilterStore, mangaListUpdatesStore}) => {
  const classes = useStyles();
  useEffect(()=>{
    mangaListUpdatesStore.fetchMangaChank()
    return mangaListUpdatesStore.clear
  },[])
  const [isFetching, setIsFetching] = useInfiniteScroll(mangaListUpdatesStore.nextMangaChank);
  useEffect(()=>{
    if(isFetching!==mangaListUpdatesStore.isLoading){
      setIsFetching(mangaListUpdatesStore.isLoading)
    }

  },[mangaListUpdatesStore.isLoading,isFetching])
  return(
    <>
    <div className={classes.topGroup}>
      <Hidden xsDown>
        <Typography color="textPrimary" variant="h6" component="span">
          Список манги <Typography variant="h6" color="textSecondary" component="span"> ({mangaListUpdatesStore.total}) </Typography>
        </Typography>
      </Hidden>
      <Typography color="textSecondary" style={{fontWeight: "450", display: "flex", alignItems: "baseline"}}>
        Сортировка по:

        <FormControl className={classes.formControl}>
          <Select
            textColor="primary"
            value={mangaListUpdatesStore.sortBy}
            onChange={evt=>mangaListUpdatesStore.setSortBy(evt.target.value)}
            // labelWidth={labelWidth}
            input={<FilterInput />}
          >
            {
              sortParams.map(sortParam=><MenuItem id={`sort_by_${sortParam.value}`} value={sortParam.value}>{sortParam.name}</MenuItem>)
            }
          </Select>
          </FormControl>
      </Typography>
        <Hidden mdUp>
          <RMenu mangaFilterStore={mangaFilterStore}/>
        </Hidden>
        <Hidden smDown>
          <Typography color="textSecondary" style={{fontWeight: "450", width: 234}}>
            Фильтр ({mangaFilterStore.cnt||0})
            { 
            <Button onClick={()=>mangaFilterStore.clearAll("MangaListUpdatesStore")} disabled={mangaFilterStore.cnt===0} className={classes.filterResetButton} variant="outlined" size="small" color="secondary">
              Сбросить
            </Button>
            }
          </Typography>
        </Hidden>  
    </div>
    <div style={{display: "flex"}}>
    <Hidden mdUp>
      <div style={{width: "100%"}}>
        <MangaUpdateList {...mangaListUpdatesStore} isLoading={mangaListUpdatesStore.isLoading}/>
      </div>
    </Hidden>

    <Hidden smDown>
        <div style={{width: "calc(100% - 250px)"}}>
        <MangaUpdateList {...mangaListUpdatesStore} isLoading={mangaListUpdatesStore.isLoading}/>
        </div>
        <div style={{width: 250}}>
          <Filter mangaFilterStore={mangaFilterStore} storeName={"MangaListStore"}/>
        </div>
      </Hidden>
    </div>

    </>
)})



export default inject("mangaFilterStore", "mangaListUpdatesStore")(MangaListPage);
